<template>
  <div class="blog-detail">
    <h1>{{ blog?.title || 'åŠ è½½ä¸­...' }}</h1>
    <div v-if="blog" class="blog-content">
      <div class="blog-meta">
          <span>ä½œè€…: {{ blog.author }}</span>
          <span>åˆ›å»ºæ—¶é—´: {{ formatDate(blog.createdAt) }}</span>
          <span v-if="blog.updatedAt !== blog.createdAt">æ›´æ–°æ—¶é—´: {{ formatDate(blog.updatedAt) }}</span>
          <span>çŠ¶æ€: {{ blog.status === 'published' ? 'å·²å‘å¸ƒ' : 'è‰ç¨¿' }}</span>
          <span v-if="blog.source">æ¥æº: {{ blog.source }}</span>
        </div>
        <!-- ç®€åŒ–çš„æ–‡ç« ç»Ÿè®¡ä¿¡æ¯ -->
        <div class="simple-stats">
          <span class="stat-item">
            <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
              <path d="M12 4.5C7 4.5 2.73 7.61 1 12c1.73 4.39 6 7.5 11 7.5s9.27-3.11 11-7.5c-1.73-4.39-6-7.5-11-7.5zM12 17c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z"/>
            </svg>
            {{ blog.viewCount || 0 }}
          </span>
          <span class="stat-item">
            <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
              <path d="M20 2H4c-1.1 0-2 .9-2 2v12c0 1.1.9 2 2 2h14l4 4V4c0-1.1-.9-2-2-2zm-2 12H6v-2h12v2zm0-3H6V9h12v2zm0-3H6V6h12v2z"/>
            </svg>
            {{ blog.commentCount || 0 }}
          </span>
          <span class="stat-item">
            <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
              <path d="M12 8l-6 6 1.41 1.41L12 10.83l4.59 4.58L18 14l-6-6z"/>
            </svg>
            {{ blog.forwardCount || 0 }}
          </span>
          <!-- ç‚¹èµä¿¡æ¯ç§»åˆ°è¿™é‡Œ -->
          <span class="stat-item likes">
            <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
              <path d="M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z"/>
            </svg>
            {{ blog.likeCount || 0 }}
          </span>
        </div>
      
      <!-- æ ‡ç­¾æ˜¾ç¤º -->
      <div class="blog-tags" v-if="blog.tags && blog.tags.length > 0">
        <span class="tag" v-for="tag in blog.tags" :key="tag.id || tag">
          # {{ tag.name || tag }}
        </span>
      </div>
      
      <div class="content">
        <p>{{ blog.content }}</p>
      </div>
      
      <!-- äº¤äº’æŒ‰é’® -->
        <div class="interaction-actions">
          <LikeButton 
            :blog-id="blog.id" 
            :initial-like-count="blog.likeCount || 0"
            :initial-is-liked="blog.isLiked || false"
            @like-change="handleLikeChange"
            class="simple-like-button"
          />
          <FavoriteButton :blog-id="blog.id" />
          <button class="btn" @click="goBack">è¿”å›åˆ—è¡¨</button>
        </div>
      
      <!-- è¯„è®ºåˆ—è¡¨ -->
      <CommentList :blog-id="blog.id" />
    </div>
    <div v-else-if="!loading" class="error">
      åšå®¢ä¸å­˜åœ¨æˆ–å·²è¢«åˆ é™¤
    </div>
  </div>
</template>

<script>
import { getBlogDetail } from '../api/blog';
import { addReadHistory, updateReadProgress } from '../api/history';
import LikeButton from '../components/LikeButton.vue';
import FavoriteButton from '../components/FavoriteButton.vue';
import CommentList from '../components/CommentList.vue';

export default {
  name: 'BlogDetail',
  components: {
    LikeButton,
    FavoriteButton,
    CommentList
  },
  data() {
    return {
      blog: null,
      loading: true,
      progressCheckInterval: null
    }
  },
  created() {
    this.loadBlogDetail();
  },
  mounted() {
    // æ·»åŠ æ»šåŠ¨è¿›åº¦è¿½è¸ª
    window.addEventListener('scroll', this.handleScroll);
    // è®¾ç½®å®šæ—¶ä¿å­˜é˜…è¯»è¿›åº¦
    this.progressCheckInterval = setInterval(() => {
      this.saveReadProgress();
    }, 10000); // æ¯10ç§’ä¿å­˜ä¸€æ¬¡è¿›åº¦
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
    if (this.progressCheckInterval) {
      clearInterval(this.progressCheckInterval);
    }
    // ç¦»å¼€é¡µé¢æ—¶ä¿å­˜æœ€ç»ˆé˜…è¯»è¿›åº¦
    this.saveReadProgress(100);
  },
  methods: {
    async loadBlogDetail() {
      try {
        const id = this.$route.params.id;
        const response = await getBlogDetail(id);
        this.blog = response.data;
        // æ·»åŠ åˆ°é˜…è¯»å†å²
        await addReadHistory(parseInt(id));
      } catch (error) {
        console.error('è·å–åšå®¢è¯¦æƒ…å¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    // å¤„ç†ç‚¹èµçŠ¶æ€å˜åŒ–
    handleLikeChange(likeData) {
      // æ›´æ–°å½“å‰åšå®¢çš„ç‚¹èµæ•°æ®
      this.blog.likeCount = likeData.likeCount;
      this.blog.isLiked = likeData.isLiked;
    },
    goBack() {
      this.$router.push('/');
    },
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    handleScroll() {
      // å¯ä»¥åœ¨è¿™é‡Œå®æ—¶æ›´æ–°é˜…è¯»è¿›åº¦
    },
    async saveReadProgress(forceProgress = null) {
      if (!this.blog) return;
      
      try {
        let progress = forceProgress;
        if (progress === null) {
          // è®¡ç®—å½“å‰é˜…è¯»è¿›åº¦
          const scrollTop = window.scrollY;
          const docHeight = document.documentElement.scrollHeight - window.innerHeight;
          progress = docHeight > 0 ? Math.min(100, Math.round((scrollTop / docHeight) * 100)) : 0;
        }
        
        // åªä¿å­˜æœ‰æ„ä¹‰çš„è¿›åº¦ï¼ˆè‡³å°‘10%æˆ–å¼ºåˆ¶ä¿å­˜ï¼‰
        if (progress >= 10 || forceProgress !== null) {
          await updateReadProgress(this.blog.id, progress);
        }
      } catch (error) {
        console.error('ä¿å­˜é˜…è¯»è¿›åº¦å¤±è´¥:', error);
      }
    }
  }
}
</script>

