import axios from 'axios';

// ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®æ ‡å¿—
const USE_MOCK_DATA = true;

// åˆ›å»ºaxioså®ä¾‹
const api = axios.create({
  baseURL: '/api',
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json'
  }
});

// æ¨¡æ‹Ÿæ”¶è—æ•°æ®
const mockFavorites = [
  { id: 1, blogId: 1, userId: 'user1', createdAt: '2024-03-15T10:30:00' },
  { id: 2, blogId: 2, userId: 'user1', createdAt: '2024-03-16T14:20:00' },
  { id: 3, blogId: 5, userId: 'user1', createdAt: '2024-03-17T09:15:00' }
];

// æ¨¡æ‹Ÿç”¨æˆ·IDï¼ˆå®é™…åº”è¯¥ä»è®¤è¯ç³»ç»Ÿè·å–ï¼‰
const currentUserId = 'user1';

// å»¶è¿Ÿå‡½æ•°ï¼Œæ¨¡æ‹Ÿç½‘ç»œè¯·æ±‚
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * è·å–ç”¨æˆ·çš„æ”¶è—åˆ—è¡¨
 * @returns {Promise} æ”¶è—åˆ—è¡¨
 */
export const getUserFavorites = async () => {
  if (USE_MOCK_DATA) {
    await delay(300);
    return { data: mockFavorites.filter(fav => fav.userId === currentUserId) };
  }
  return api.get('/favorites');
};

/**
 * æ£€æŸ¥æ–‡ç« æ˜¯å¦å·²æ”¶è—
 * @param {number} blogId åšå®¢ID
 * @returns {Promise} æ˜¯å¦å·²æ”¶è—
 */
export const checkFavoriteStatus = async (blogId) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const isFavorite = mockFavorites.some(fav => fav.userId === currentUserId && fav.blogId === blogId);
    return { data: { isFavorite } };
  }
  return api.get(`/favorites/check/${blogId}`);
};

/**
 * æ·»åŠ æ”¶è—
 * @param {number} blogId åšå®¢ID
 * @returns {Promise} æ·»åŠ ç»“æœ
 */
export const addFavorite = async (blogId) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const newId = Math.max(...mockFavorites.map(fav => fav.id), 0) + 1;
    const newFavorite = {
      id: newId,
      blogId,
      userId: currentUserId,
      createdAt: new Date().toISOString()
    };
    mockFavorites.push(newFavorite);
    return { data: newFavorite };
  }
  return api.post('/favorites', { blogId });
};

/**
 * å–æ¶ˆæ”¶è—
 * @param {number} blogId åšå®¢ID
 * @returns {Promise} å–æ¶ˆç»“æœ
 */
export const removeFavorite = async (blogId) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const index = mockFavorites.findIndex(fav => fav.userId === currentUserId && fav.blogId === blogId);
    if (index !== -1) {
      mockFavorites.splice(index, 1);
    }
    return { data: { success: true } };
  }
  return api.delete(`/favorites/${blogId}`);
};
