<template>
  <button 
    class="favorite-btn" 
    :class="{ favorited: isFavorited }" 
    @click="toggleFavorite"
    :disabled="loading"
  >
    <span class="icon">
      {{ isFavorited ? 'â˜…' : 'â˜†' }}
    </span>
    <span class="text">{{ isFavorited ? 'å·²æ”¶è—' : 'æ”¶è—' }}</span>
    <span v-if="loading" class="loading">...</span>
  </button>
</template>

<script>
import { checkFavoriteStatus, addFavorite, removeFavorite } from '../api/favorites';

export default {
  name: 'FavoriteButton',
  props: {
    blogId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      isFavorited: false,
      loading: false
    };
  },
  created() {
    this.fetchFavoriteStatus();
  },
  methods: {
    async fetchFavoriteStatus() {
      try {
        this.loading = true;
        const response = await checkFavoriteStatus(this.blogId);
        this.isFavorited = response.data?.isFavorite || false;
      } catch (error) {
        console.error('è·å–æ”¶è—çŠ¶æ€å¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    async toggleFavorite() {
      if (this.loading) return;
      
      try {
        this.loading = true;
        
        if (this.isFavorited) {
          await removeFavorite(this.blogId);
          this.isFavorited = false;
          this.$emit('unfavorited');
        } else {
          await addFavorite(this.blogId);
          this.isFavorited = true;
          this.$emit('favorited');
        }
      } catch (error) {
        console.error('æ›´æ–°æ”¶è—çŠ¶æ€å¤±è´¥:', error);
        // å¯ä»¥åœ¨è¿™é‡Œæ·»åŠ ç”¨æˆ·å‹å¥½çš„é”™è¯¯æç¤º
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

