  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
  color: #495057;
}

.favorite-btn:hover {
  background-color: #e9ecef;
  border-color: #adb5bd;
}

.favorite-btn.favorited {
  background-color: #fff3cd;
  border-color: #ffeaa7;
  color: #856404;
}

.favorite-btn.favorited:hover {
  background-color: #ffeaa7;
}

.favorite-btn .icon {
  font-size: 16px;
}

.favorite-btn .loading {
  margin-left: 4px;
  font-size: 12px;
  color: #6c757d;
}

.favorite-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
</style>
