<template>
  <div class="comment-section">
    <h3 class="section-title">è¯„è®º ({{ comments.length }})</h3>
    
    <!-- è¯„è®ºè¾“å…¥æ¡† -->
    <div class="comment-input-wrapper">
          <div class="comment-input-box">
            <!-- è¯„è®ºå›¾ç‰‡é¢„è§ˆ -->
            <div v-if="commentImages && commentImages.length > 0" class="comment-images-preview">
              <div 
                v-for="(image, index) in commentImages" 
                :key="`preview-${index}`" 
                class="comment-image-preview-item"
              >
                <img 
                  :src="image.previewUrl" 
                  :alt="image.fileName" 
                  class="comment-image-preview"
                >
                <button 
                  class="remove-image-btn"
                  @click="removeCommentImage(index)"
                  :disabled="submitting || uploadingImage"
                >
                  Ã—
                </button>
              </div>
            </div>
            
            <textarea 
              v-model="newComment" 
              placeholder="å†™ä¸‹ä½ çš„è¯„è®º..."
              rows="3"
              @keydown.ctrl.enter="submitComment"
              @input="handleAtInput($event, null)"
              :disabled="uploadingImage"
            ></textarea>
            
            <div class="comment-actions">
              <span class="hint">æŒ‰ Ctrl+Enter å‘é€</span>
              
              <!-- å›¾ç‰‡ä¸Šä¼ æŒ‰é’® -->
              <label 
                class="upload-image-btn"
                :disabled="commentImages.length >= maxImages || uploadingImage"
              >
                ğŸ“· {{ commentImages.length }}/{{ maxImages }}
                <input 
                  type="file" 
                  accept="image/*" 
                  multiple 
                  style="display: none" 
                  @change="handleImageSelect"
                  :disabled="commentImages.length >= maxImages || uploadingImage"
                >
                <span v-if="uploadingImage" class="uploading-indicator">ä¸Šä¼ ä¸­...</span>
              </label>
              
              <button 
                class="submit-btn" 
                @click="submitComment"
                :disabled="(!newComment.trim() && commentImages.length === 0) || submitting || uploadingImage"
              >
                {{ submitting ? 'æäº¤ä¸­...' : 'å‘è¡¨è¯„è®º' }}
              </button>
            </div>
          </div>
        </div>
    
    <!-- è¯„è®ºåˆ—è¡¨ -->
    <div class="comment-list" v-if="comments.length > 0">
      <div v-for="comment in comments" :key="comment.id" class="comment-item">
        <div class="comment-avatar">
          <img :src="comment.avatar || '/avatar/default.png'" :alt="comment.userName || 'åŒ¿åç”¨æˆ·'" class="user-avatar">
        </div>
        <div class="comment-body">
          <div class="comment-header">
            <span class="user-name">{{ comment.userName || 'åŒ¿åç”¨æˆ·' }}</span>
            <span class="user-level" :class="`level-${comment.level || 1}`">Lv.{{ comment.level || 1 }}</span>
            <span class="comment-time">{{ formatTime(comment.createdAt) }}</span>
          </div>
          <div class="comment-content">{{ comment.content }}</div>
          <div class="comment-footer">
            <button 
              class="like-comment-btn"
              :class="{ liked: comment.isLiked }"
              @click="toggleCommentLike(comment.id)"
              :disabled="comment.liking"
            >
              <span>{{ comment.isLiked ? 'ğŸ‘' : 'ğŸ‘' }}</span>
              <span>{{ comment.likeCount || comment.likes || 0 }}</span>
              <span v-if="comment.liking">...</span>
            </button>
            <button 
              class="reply-comment-btn"
              @click="showReplyBox(comment.id)"
            >
              å›å¤
            </button>
          </div>
          
          <!-- å›å¤åˆ—è¡¨ -->
          <div v-if="comment.replies && comment.replies.length > 0" class="replies-list">
            <div 
              v-for="reply in comment.replies" 
              :key="reply.id" 
              class="reply-item"
            >
              <div class="comment-avatar">
                <img :src="reply.avatar || '/avatar/default.png'" :alt="reply.userName || 'åŒ¿åç”¨æˆ·'" class="user-avatar">
              </div>
              <div class="comment-body">
                <div class="comment-header">
                  <span class="user-name">{{ reply.userName || 'åŒ¿åç”¨æˆ·' }}</span>
                  <span class="user-level" :class="`level-${reply.level || 1}`">Lv.{{ reply.level || 1 }}</span>
                  <span class="comment-time">{{ formatTime(reply.createdAt) }}</span>
                </div>
                <div class="comment-content">{{ reply.content }}</div>
                <div class="comment-footer">
                  <button 
                    class="like-comment-btn"
                    :class="{ liked: reply.isLiked }"
                    @click="toggleReplyLike(comment.id, reply.id)"
                    :disabled="reply.liking"
                  >
                    <span>{{ reply.isLiked ? 'ğŸ‘' : 'ğŸ‘' }}</span>
                    <span>{{ reply.likes || 0 }}</span>
                    <span v-if="reply.liking">...</span>
                  </button>
                  <button 
                    class="reply-comment-btn"
                    @click="showReplyBox(comment.id, { userName: reply.userName })"
                  >
                    å›å¤
                  </button>
                </div>
              </div>
            </div>
          </div>
          
          <!-- å›å¤è¾“å…¥æ¡† -->
          <div v-if="replyStates[comment.id] && replyStates[comment.id].showReplyBox" class="reply-input-wrapper">
            <div class="reply-input-box">
              <textarea 
                :ref="`replyInput-${comment.id}`"
                :class="`reply-input-${comment.id}`"
                v-model="replyStates[comment.id].replyContent" 
                placeholder="å†™ä¸‹ä½ çš„å›å¤..."
                rows="2"
                @keydown.ctrl.enter="submitReply(comment.id)"
                @input="handleAtInput($event, comment.id)"
              ></textarea>
              <div class="comment-actions">
                <span class="hint">æŒ‰ Ctrl+Enter å‘é€</span>
                <div class="reply-buttons">
                  <button 
                    class="cancel-btn" 
                    @click="hideReplyBox(comment.id)"
                  >
                    å–æ¶ˆ
                  </button>
                  <button 
                    class="submit-btn" 
                    @click="submitReply(comment.id)"
                    :disabled="!replyStates[comment.id].replyContent.trim() || replyStates[comment.id].submitting"
                  >
                    {{ replyStates[comment.id].submitting ? 'æäº¤ä¸­...' : 'å‘è¡¨å›å¤' }}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- ç©ºçŠ¶æ€ -->
    <div v-else-if="!loading" class="empty-comments">
      æš‚æ— è¯„è®ºï¼Œå¿«æ¥å‘è¡¨ç¬¬ä¸€æ¡è¯„è®ºå§ï¼
    </div>
    
    <!-- åŠ è½½çŠ¶æ€ -->
    <div v-if="loading" class="loading-comments">
      åŠ è½½è¯„è®ºä¸­...
    </div>
  </div>
</template>

<script>
import { getBlogComments, createComment, likeComment, unlikeComment, replyToComment, likeReply, unlikeReply } from '../api/comments';

export default {
  name: 'CommentList',
  props: {
    blogId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      comments: [],
      newComment: '',
      loading: false,
      submitting: false,
      replyStates: {}, // å­˜å‚¨æ¯ä¸ªè¯„è®ºçš„å›å¤çŠ¶æ€
      atUserList: [], // ç”¨äº@åŠŸèƒ½çš„ç”¨æˆ·åˆ—è¡¨ç¼“å­˜
      commentImages: [], // è¯„è®ºå›¾ç‰‡åˆ—è¡¨
      uploadingImage: false, // å›¾ç‰‡ä¸Šä¼ çŠ¶æ€
      maxImages: 9 // æœ€å¤§å›¾ç‰‡æ•°é‡é™åˆ¶
    };
  },
  created() {
    this.fetchComments();
  },
  mounted() {
    // åˆå§‹åŒ–å›å¤çŠ¶æ€
    this.initializeReplyStates();
  },
  updated() {
    // å½“è¯„è®ºåˆ—è¡¨æ›´æ–°æ—¶ï¼Œç¡®ä¿å›å¤çŠ¶æ€ä¹Ÿæ›´æ–°
    this.initializeReplyStates();
  },
  created() {
    this.fetchComments();
  },
  methods: {
    initializeReplyStates() {
      // åˆå§‹åŒ–æ¯ä¸ªè¯„è®ºçš„å›å¤çŠ¶æ€
      this.comments.forEach(comment => {
        if (!this.replyStates[comment.id]) {
          this.$set(this.replyStates, comment.id, {
            showReplyBox: false,
            replyContent: '',
            submitting: false,
            replyingTo: null
          });
        }
        
        // ç¡®ä¿è¯„è®ºæœ‰repliesæ•°ç»„
        if (!comment.replies) {
          comment.replies = [];
        }
        
        // ä¸ºæ¯ä¸ªå›å¤æ·»åŠ æœ¬åœ°çŠ¶æ€
        comment.replies.forEach(reply => {
          if (!reply.isLiked) reply.isLiked = false;
          if (!reply.liking) reply.liking = false;
        });
      });
    },
    
    async fetchComments() {
      try {
        this.loading = true;
        const response = await getBlogComments(this.blogId);
        // ä¸ºæ¯æ¡è¯„è®ºæ·»åŠ æœ¬åœ°çŠ¶æ€
        this.comments = response.data.map(comment => ({
          ...comment,
          isLiked: comment.isLiked || false,
          likeCount: comment.likeCount || comment.likes || 0,
          liking: false,
          replies: comment.replies || []
        }));
        
        // ç¼“å­˜ç”¨æˆ·åˆ—è¡¨ï¼Œç”¨äº@åŠŸèƒ½
        this.cacheUsersForAtFeature();
      } catch (error) {
        console.error('è·å–è¯„è®ºå¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    
    cacheUsersForAtFeature() {
      // ç¼“å­˜è¯„è®ºä¸­çš„æ‰€æœ‰ç”¨æˆ·ï¼Œç”¨äº@åŠŸèƒ½
      const users = new Set();
      this.comments.forEach(comment => {
        users.add({ id: comment.userId, name: comment.userName });
        if (comment.replies) {
          comment.replies.forEach(reply => {
            users.add({ id: reply.userId, name: reply.userName });
          });
        }
      });
      this.atUserList = Array.from(users);
    },
    
    // å¤„ç†æ–‡æœ¬è¾“å…¥ä¸­çš„@ç¬¦å·ï¼Œå¼¹å‡ºç”¨æˆ·åˆ—è¡¨
    handleAtInput(event, commentId) {
      const value = event.target.value;
      const cursorPos = event.target.selectionStart;
      const textBeforeCursor = value.substring(0, cursorPos);
      const lastAtIndex = textBeforeCursor.lastIndexOf('@');
      
      if (lastAtIndex !== -1) {
        const textAfterAt = textBeforeCursor.substring(lastAtIndex + 1);
        // å¦‚æœ@åé¢æ²¡æœ‰å­—ç¬¦æˆ–ç©ºæ ¼ï¼Œæ˜¾ç¤º@ç”¨æˆ·åˆ—è¡¨UI
        if (textAfterAt === '' || /^\s*$/.test(textAfterAt)) {
          // è¿™é‡Œå¯ä»¥æ·»åŠ UIé€»è¾‘æ¥æ˜¾ç¤ºç”¨æˆ·åˆ—è¡¨ï¼Œæš‚æ—¶åªæ˜¯æ·»åŠ æ³¨é‡Š
          console.log('Show at user list');
        }
      }
    },
    
    // æ’å…¥@ç”¨æˆ·ååˆ°è¯„è®ºå†…å®¹ä¸­
    insertAtUser(userName, commentId) {
      const replyState = this.replyStates[commentId];
      if (!replyState) return;
      
      replyState.replyContent += `@${userName} `;
    },
    async submitComment() {
      if ((!this.newComment.trim() && this.commentImages.length === 0) || this.submitting) return;
      
      try {
        this.submitting = true;
        
        // å‡†å¤‡å›¾ç‰‡æ•°æ®
        const imageData = this.formatImagesForApi(this.commentImages);
        
        const response = await createComment(this.blogId, this.newComment, imageData);
        
        if (response.data) {
          // ä¸ºæ–°è¯„è®ºæ·»åŠ æœ¬åœ°çŠ¶æ€
          const newComment = {
            ...response.data,
            isLiked: false,
            likeCount: 0,
            liking: false,
            replies: [],
            images: imageData
          };
          
          this.comments.unshift(newComment);
          this.newComment = '';
          
          // æ¸…ç©ºå›¾ç‰‡åˆ—è¡¨å¹¶é‡Šæ”¾èµ„æº
          this.commentImages.forEach(img => {
            if (img.previewUrl) {
              URL.revokeObjectURL(img.previewUrl);
            }
          });
          this.commentImages = [];
          
          // é‡ç½®@ç”¨æˆ·åˆ—è¡¨ç¼“å­˜
          this.cacheUsersForAtFeature();
        }
      } catch (error) {
        console.error('æäº¤è¯„è®ºå¤±è´¥:', error);
      } finally {
        this.submitting = false;
      }
    },
    
    // æ˜¾ç¤ºå›¾ç‰‡é¢„è§ˆ
    showImagePreview(imageUrl) {
      // åœ¨å®é™…åº”ç”¨ä¸­ï¼Œè¿™é‡Œå¯ä»¥è°ƒç”¨å›¾ç‰‡æŸ¥çœ‹å™¨ç»„ä»¶
      // ç›®å‰ç®€å•åœ°åœ¨æ–°çª—å£æ‰“å¼€å›¾ç‰‡
      window.open(imageUrl, '_blank');
    },
    
    // å¤„ç†å›¾ç‰‡é€‰æ‹©
    async handleImageSelect(event) {
      const files = Array.from(event.target.files);
      if (files.length === 0) return;
      
      // æ£€æŸ¥æ˜¯å¦è¶…è¿‡æœ€å¤§å›¾ç‰‡æ•°é‡
      const remainingSlots = this.maxImages - this.commentImages.length;
      if (files.length > remainingSlots) {
        alert(`æœ€å¤šåªèƒ½ä¸Šä¼ ${this.maxImages}å¼ å›¾ç‰‡ï¼Œè¿˜å¯ä»¥ä¸Šä¼ ${remainingSlots}å¼ `);
        return;
      }
      
      this.uploadingImage = true;
      
      try {
        // ä¸ºæ¯ä¸ªæ–‡ä»¶åˆ›å»ºé¢„è§ˆ
        for (const file of files) {
          // æ£€æŸ¥æ–‡ä»¶ç±»å‹
          if (!file.type.startsWith('image/')) {
            alert('è¯·ä¸Šä¼ å›¾ç‰‡æ–‡ä»¶');
            continue;
          }
          
          // æ£€æŸ¥æ–‡ä»¶å¤§å°ï¼ˆé™åˆ¶5MBï¼‰
          if (file.size > 5 * 1024 * 1024) {
            alert('å›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡5MB');
            continue;
          }
          
          // åˆ›å»ºé¢„è§ˆURL
          const previewUrl = URL.createObjectURL(file);
          
          // æ·»åŠ åˆ°å›¾ç‰‡åˆ—è¡¨
          this.commentImages.push({
            file: file,
            previewUrl: previewUrl,
            fileName: file.name
          });
        }
      } catch (error) {
        console.error('å¤„ç†å›¾ç‰‡å¤±è´¥:', error);
        alert('å¤„ç†å›¾ç‰‡å¤±è´¥ï¼Œè¯·é‡è¯•');
      } finally {
        this.uploadingImage = false;
        // æ¸…ç©ºæ–‡ä»¶è¾“å…¥ï¼Œå…è®¸é‡å¤é€‰æ‹©ç›¸åŒæ–‡ä»¶
        event.target.value = '';
      }
    },
    
    // ç§»é™¤è¯„è®ºå›¾ç‰‡
    removeCommentImage(index) {
      const image = this.commentImages[index];
      if (image.previewUrl) {
        URL.revokeObjectURL(image.previewUrl);
      }
      this.commentImages.splice(index, 1);
    },
    
    // æ ¼å¼åŒ–å›¾ç‰‡æ•°æ®ç”¨äºAPI
    formatImagesForApi(images) {
      // åœ¨å®é™…åº”ç”¨ä¸­ï¼Œè¿™é‡Œåº”è¯¥å¤„ç†å›¾ç‰‡ä¸Šä¼ åˆ°æœåŠ¡å™¨
      // ç›®å‰ç®€å•è¿”å›å›¾ç‰‡ä¿¡æ¯
      return images.map(img => ({
        fileName: img.fileName,
        url: img.previewUrl, // ä¸´æ—¶URLï¼Œå®é™…åº”ç”¨ä¸­åº”è¯¥æ˜¯æœåŠ¡å™¨è¿”å›çš„URL
        size: img.file ? img.file.size : 0
      }));
    },
    async toggleCommentLike(commentId) {
      const comment = this.comments.find(c => c.id === commentId);
      if (!comment || comment.liking) return;
      
      try {
        comment.liking = true;
        
        let response;
        if (comment.isLiked) {
          response = await unlikeComment(commentId);
        } else {
          response = await likeComment(commentId);
        }
        
        if (response.data) {
          comment.isLiked = response.data.isLiked;
          comment.likeCount = response.data.likeCount || comment.likeCount + (comment.isLiked ? 1 : -1);
        }
      } catch (error) {
        console.error('ç‚¹èµè¯„è®ºå¤±è´¥:', error);
      } finally {
        comment.liking = false;
      }
    },
    
    async toggleReplyLike(commentId, replyId) {
      const comment = this.comments.find(c => c.id === commentId);
      if (!comment) return;
      
      const reply = comment.replies.find(r => r.id === replyId);
      if (!reply || reply.liking) return;
      
      try {
        reply.liking = true;
        
        let response;
        if (reply.isLiked) {
          response = await unlikeReply(commentId, replyId);
        } else {
          response = await likeReply(commentId, replyId);
        }
        
        if (response.data) {
          reply.isLiked = !reply.isLiked;
          reply.likes = reply.likes + (reply.isLiked ? 1 : -1);
        }
      } catch (error) {
        console.error('ç‚¹èµå›å¤å¤±è´¥:', error);
      } finally {
        reply.liking = false;
      }
    },
    
    // æ˜¾ç¤ºå›å¤æ¡†
    showReplyBox(commentId, replyTo = null) {
      if (!this.replyStates[commentId]) {
        this.$set(this.replyStates, commentId, {
          showReplyBox: false,
          replyContent: '',
          submitting: false,
          replyingTo: null
        });
      }
      
      this.replyStates[commentId].showReplyBox = true;
      this.replyStates[commentId].replyingTo = replyTo;
      
      // å¦‚æœæ˜¯å›å¤ç‰¹å®šç”¨æˆ·ï¼Œé¢„å¡«@ç”¨æˆ·å
      if (replyTo) {
        this.replyStates[commentId].replyContent = `@${replyTo.userName} `;
      } else {
        this.replyStates[commentId].replyContent = '';
      }
      
      // å»¶è¿Ÿèšç„¦åˆ°å›å¤æ¡†
      this.$nextTick(() => {
        const replyBox = document.querySelector(`.reply-input-${commentId}`);
        if (replyBox) {
          replyBox.focus();
        }
      });
    },
    
    // éšè—å›å¤æ¡†
    hideReplyBox(commentId) {
      if (this.replyStates[commentId]) {
        this.replyStates[commentId].showReplyBox = false;
        this.replyStates[commentId].replyContent = '';
        this.replyStates[commentId].replyingTo = null;
      }
    },
    
    // æäº¤å›å¤
    async submitReply(commentId) {
      const replyState = this.replyStates[commentId];
      if (!replyState || !replyState.replyContent.trim() || replyState.submitting) {
        return;
      }
      
      try {
        replyState.submitting = true;
        const content = replyState.replyContent.trim();
        const replyToUser = replyState.replyingTo ? replyState.replyingTo.userName : null;
        
        const response = await replyToComment(commentId, content, replyToUser);
        
        if (response.data) {
          const comment = this.comments.find(c => c.id === commentId);
          if (comment) {
            comment.replies.push({
              ...response.data,
              isLiked: false,
              liking: false
            });
          }
          
          // éšè—å›å¤æ¡†
          this.hideReplyBox(commentId);
        }
      } catch (error) {
        console.error('å›å¤è¯„è®ºå¤±è´¥:', error);
      } finally {
        replyState.submitting = false;
      }
    },
    formatTime(timeStr) {
      if (!timeStr) return '';
      
      const date = new Date(timeStr);
      const now = new Date();
      const diff = now - date;
      
      // å°äº1åˆ†é’Ÿ
      if (diff < 60 * 1000) {
        return 'åˆšåˆš';
      }
      // å°äº1å°æ—¶
      if (diff < 60 * 60 * 1000) {
        return `${Math.floor(diff / (60 * 1000))}åˆ†é’Ÿå‰`;
      }
      // å°äº24å°æ—¶
      if (diff < 24 * 60 * 60 * 1000) {
        return `${Math.floor(diff / (60 * 60 * 1000))}å°æ—¶å‰`;
      }
      // å°äº30å¤©
      if (diff < 30 * 24 * 60 * 60 * 1000) {
        return `${Math.floor(diff / (24 * 60 * 60 * 1000))}å¤©å‰`;
      }
      
      // æ ¼å¼åŒ–æ—¥æœŸ
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
    }
  }
};
</script>

<style scoped>
