.comment-section {
  margin-top: 40px;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  overflow: hidden;
}

/* æ ‡é¢˜æ ·å¼ */
.section-title {
  padding: 24px 24px 0;
  font-size: 24px;
  font-weight: 700;
  color: #1a1a1a;
  display: flex;
  align-items: center;
  gap: 12px;
  position: relative;
  padding-bottom: 16px;
  letter-spacing: -0.5px;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 24px;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #007bff, #6610f2);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.comment-section:hover .section-title::after {
  width: 80px;
}

/* è¯„è®ºè¾“å…¥åŒºåŸŸ */
.comment-input-wrapper {
  padding: 24px;
  background-color: #f8fafc;
  border-bottom: 1px solid #e2e8f0;
  animation: inputSlideIn 0.6s ease-out;
}

@keyframes inputSlideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.comment-input-box {
  background-color: #ffffff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
}

.comment-input-box:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.comment-input-box textarea {
  width: 100%;
  border: none;
  resize: vertical;
  font-size: 15px;
