<template>
  <button 
    class="like-btn" 
    :class="{ liked: isLiked }" 
    @click="toggleLike"
    :disabled="loading"
  >
    <span class="icon">
      {{ isLiked ? 'â¤ï¸' : 'ğŸ¤' }}
    </span>
    <span class="count">{{ likeCount || 0 }}</span>
    <span v-if="loading" class="loading">...</span>
  </button>
</template>

<script>
import { getBlogLikeStatus, likeBlog, unlikeBlog } from '../api/likes';

export default {
  name: 'LikeButton',
  props: {
    blogId: {
      type: Number,
      required: true
    },
    initialLikeCount: {
      type: Number,
      default: 0
    },
    initialIsLiked: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isLiked: false,
      likeCount: 0,
      loading: false
    };
  },
  created() {
    // ä¼˜å…ˆä½¿ç”¨çˆ¶ç»„ä»¶æä¾›çš„åˆå§‹æ•°æ®
    if (this.initialLikeCount !== undefined) {
      this.likeCount = this.initialLikeCount;
      this.isLiked = this.initialIsLiked;
    } else {
      this.fetchLikeStatus();
    }
  },
  methods: {
    async fetchLikeStatus() {
      try {
        this.loading = true;
        const response = await getBlogLikeStatus(this.blogId);
        this.isLiked = response.data?.isLiked || false;
        this.likeCount = response.data?.likeCount || 0;
      } catch (error) {
        console.error('è·å–ç‚¹èµçŠ¶æ€å¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    async toggleLike() {
      if (this.loading) return;
      
      try {
        this.loading = true;
        
        let response;
        if (this.isLiked) {
          response = await unlikeBlog(this.blogId);
        } else {
          response = await likeBlog(this.blogId);
        }
        
        if (response.data) {
          this.isLiked = response.data.isLiked;
          this.likeCount = response.data.likeCount || this.likeCount + (this.isLiked ? 1 : -1);
          
          // å‘å‡ºäº‹ä»¶é€šçŸ¥çˆ¶ç»„ä»¶ï¼Œç»Ÿä¸€ä½¿ç”¨like-changeäº‹ä»¶å
          this.$emit('like-change', { 
            blogId: this.blogId, 
            isLiked: this.isLiked,
            likeCount: this.likeCount 
          });
        }
      } catch (error) {
        console.error('æ›´æ–°ç‚¹èµçŠ¶æ€å¤±è´¥:', error);
        // å¯ä»¥åœ¨è¿™é‡Œæ·»åŠ ç”¨æˆ·å‹å¥½çš„é”™è¯¯æç¤º
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

