  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
  color: #495057;
}

.like-btn:hover {
  background-color: #e9ecef;
  border-color: #adb5bd;
}

.like-btn.liked {
  background-color: #f8d7da;
  border-color: #f5c6cb;
  color: #721c24;
}

.like-btn.liked:hover {
  background-color: #f5c6cb;
}

.like-btn .icon {
  font-size: 16px;
}

.like-btn .count {
  font-weight: 500;
  min-width: 20px;
  text-align: center;
}

.like-btn .loading {
  margin-left: 4px;
  font-size: 12px;
  color: #6c757d;
}

.like-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
</style>
