// æ¨¡æ‹Ÿç”¨æˆ·ä¿¡æ¯
const currentUserId = 'user1';
const currentUserName = 'å½“å‰ç”¨æˆ·';
const currentUserAvatar = '/avatar/current_user.png';
const currentUserLevel = 4;

// å»¶è¿Ÿå‡½æ•°ï¼Œæ¨¡æ‹Ÿç½‘ç»œè¯·æ±‚
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * è·å–åšå®¢çš„è¯„è®ºåˆ—è¡¨
 * @param {number} blogId åšå®¢ID
 * @returns {Promise} è¯„è®ºåˆ—è¡¨
 */
export const getBlogComments = async (blogId) => {
  if (USE_MOCK_DATA) {
    await delay(400);
    return { 
      data: mockComments.filter(comment => comment.blogId === blogId)
        .sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt))
    };
  }
  return api.get(`/comments/blog/${blogId}`);
};

/**
 * åˆ›å»ºè¯„è®º
 * @param {number} blogId åšå®¢ID
 * @param {string} content è¯„è®ºå†…å®¹
 * @param {Array} images å›¾ç‰‡æ•°ç»„
 * @returns {Promise} åˆ›å»ºç»“æœ
 */
export const createComment = async (blogId, content, images = []) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const newId = Math.max(...mockComments.map(c => c.id), 0) + 1;
    const newComment = {
      id: newId,
      blogId,
      userId: currentUserId,
      userName: currentUserName,
      avatar: currentUserAvatar,
      level: currentUserLevel,
      content,
      likes: 0,
      createdAt: new Date().toISOString(),
      isLiked: false,
      images,
      replies: []
    };
    mockComments.push(newComment);
    return { data: newComment };
  }
  return api.post('/comments', { blogId, content, images });
};

/**
 * ä¸Šä¼ è¯„è®ºå›¾ç‰‡
 * @param {File} imageFile å›¾ç‰‡æ–‡ä»¶
 * @returns {Promise} ä¸Šä¼ ç»“æœ
 */
export const uploadCommentImage = async (imageFile) => {
  if (USE_MOCK_DATA) {
    // æ¨¡æ‹Ÿæ–‡ä»¶ä¸Šä¼ å»¶è¿Ÿ
    await delay(1000);
    
    // è¿™é‡Œæ˜¯æ¨¡æ‹Ÿè¿”å›çš„å›¾ç‰‡URLï¼Œå®é™…åº”ç”¨ä¸­åº”è¯¥æ˜¯æœåŠ¡å™¨è¿”å›çš„çœŸå®URL
    const imageUrl = `https://picsum.photos/seed/${Date.now()}/400/300`;
    
    return { 
      data: { 
        url: imageUrl,
        thumbnailUrl: imageUrl, // ç¼©ç•¥å›¾URL
        width: 400, 
        height: 300,
        fileName: imageFile.name,
        fileSize: imageFile.size
      } 
    };
  }
  const formData = new FormData();
  formData.append('image', imageFile);
  return api.post('/comments/upload-image', formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
};

/**
 * å›å¤è¯„è®º
 * @param {number} parentId çˆ¶è¯„è®ºID
 * @param {string} content å›å¤å†…å®¹
 * @param {string} replyToUser å›å¤çš„ç”¨æˆ·å
 * @param {Array} images å›¾ç‰‡æ•°ç»„
 * @returns {Promise} åˆ›å»ºç»“æœ
 */
export const replyToComment = async (parentId, content, replyToUser, images = []) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    // æŸ¥æ‰¾çˆ¶è¯„è®º
    const parentComment = mockComments.find(c => c.id === parentId);
    if (!parentComment) {
      throw new Error('çˆ¶è¯„è®ºä¸å­˜åœ¨');
    }
    
    // ç”Ÿæˆæ–°çš„å›å¤ID
    let maxReplyId = 0;
    mockComments.forEach(comment => {
      if (comment.replies && comment.replies.length > 0) {
        const repliesMaxId = Math.max(...comment.replies.map(r => r.id), 0);
        maxReplyId = Math.max(maxReplyId, repliesMaxId);
      }
    });
    
    // åˆ›å»ºå›å¤å†…å®¹ï¼Œæ”¯æŒ@ç”¨æˆ·
    const formattedContent = replyToUser ? `@${replyToUser} ${content}` : content;
    
    const newReply = {
      id: maxReplyId + 1,
      parentId,
      userId: currentUserId,
      userName: currentUserName,
      avatar: currentUserAvatar,
      level: currentUserLevel,
      content: formattedContent,
      likes: 0,
      createdAt: new Date().toISOString(),
      isLiked: false,
      images
    };
    
    parentComment.replies.push(newReply);
    return { data: newReply };
  }
  return api.post(`/comments/${parentId}/replies`, { content, replyToUser, images });
};

/**
 * ç‚¹èµå›å¤
 * @param {number} commentId è¯„è®ºID
 * @param {number} replyId å›å¤ID
 * @returns {Promise} ç‚¹èµç»“æœ
 */
export const likeReply = async (commentId, replyId) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const comment = mockComments.find(c => c.id === commentId);
    if (comment) {
      const reply = comment.replies.find(r => r.id === replyId);
      if (reply) {
        reply.likes += 1;
        reply.isLiked = true;
      }
    }
    return { data: { success: true } };
  }
  return api.post(`/comments/${commentId}/replies/${replyId}/like`);
};

/**
 * å–æ¶ˆç‚¹èµå›å¤
 * @param {number} commentId è¯„è®ºID
 * @param {number} replyId å›å¤ID
 * @returns {Promise} å–æ¶ˆç‚¹èµç»“æœ
 */
export const unlikeReply = async (commentId, replyId) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const comment = mockComments.find(c => c.id === commentId);
    if (comment) {
      const reply = comment.replies.find(r => r.id === replyId);
      if (reply && reply.likes > 0) {
        reply.likes -= 1;
        reply.isLiked = false;
      }
    }
    return { data: { success: true } };
  }
  return api.delete(`/comments/${commentId}/replies/${replyId}/like`);
};

